.navbar-container{
    height: 60px;
    background-color: #4B4453;
    color: aliceblue;
    
}
.navbar-wrapper{
    padding: 10px 20px;
    display: flex;
    align-items: center;
    justify-content: space-between;
}
.navbar-left{
    flex: 1;
    display: flex;
    align-items: center;
}
.searchcontainer{
    /* border: 0.5px solid lightgray; */
    display: flex;
    align-items: center;
    margin-left: 20px;
    padding: 5px;
}
.navbarinput{
    border: none;
    height: 32px;
    width: 320px;
    
}
.navbar-center{
    flex: 1;
    text-align: center;
}
.navbar-logo{
    font-weight: bold;
    font-size: larger;
}
.navbar-right{
    flex: 1;
    display: flex;
    align-items: center;
    justify-content: flex-end;
}
.menuitem{
    font-size: 14px;
    cursor: pointer;
    margin-left: 25px;
}