.slider-container{
    width: 100%;
    height: 100vh;
    display: flex;
    position: relative;
  overflow: hidden;
}

.arrowleft{
    width: 50px;
    height: 50px;
    background-color: white;
    border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  position: absolute;
  top: 0;
  bottom: 0;
  margin: auto;
  cursor: pointer;
  opacity: 0.5;
  z-index: 2;
}
#left{
    left:10px;
}
#right{
    right: 10px;
}
.slider-wrapper{
    height: 100%;
    display: flex;
    transition: all 1.5s ease;
}
.slide{
    width: 100vw;
    height: 100vh;
    display: flex;
    align-items: center;
}
.imagecontainer{
    flex: 1;
    height: 100%;
}
.imgslide{
    height: 100%;
    width: 100%;
}
.slideinfo{
    flex: 1;
  padding: 50px;
  display: flex;
  align-items: center;
  flex-direction: column;
}
.titleslide{
 font-size: 70px;
 text-align: center;
}
.desc{
    margin: 50px 0px;
    font-size: 20px;
    font-weight: 500;
    letter-spacing: 3px;
    text-align: center;
}
.slideinfo button{
    background-color: red;
    color: white;
    border: 2px;
    border-color: white;
    border-radius: 5px;
   
    
}
.slideinfo button:hover{
    background-color: rgb(213, 108, 108);
}