.footer-container{
    display: flex;
    background-color: #4B4453;
    color: aliceblue;
    
}
.footer-left{
    flex: 2;
    display: flex;
    flex-direction: column;
    padding: 10px;
}
.logo-footer{
  font-weight: 800;
  font-size: large;
  text-align: center;
}
.desc-footer{
    margin: 12px 0px;
}
.social-container{
display: flex;
}
.social-icon{
    width: 40px;
    height: 40px;
    border-radius: 50%;
    color: white;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-right: 20px;
    background-color: grey;
   
}
.footer-right{
    flex: 1;
    padding: 20px;
    padding-left: 80px;
}
.footer-contacttitle{
 font-weight: 700;text-align: center;
}
.contactitem{
    margin-bottom: 20px;
    
}
